import React from 'react';
import styles from './Users.module.css';
import userPhoto from '../../Assets/Images/nophoto.jpg';
import { NavLink } from 'react-router-dom';
//import { Paginator } from '../common/Paginator/Paginator';

export const User = ({ user,followingInProgress,unfollow,follow }) => {	
	return (
		<div>
			<span>
				<div>
					<NavLink to={'/profile/' + user.id}>
						<img alt="ph" src={user.photos.small != null ? user.photos.small : userPhoto} className={styles.photo} />
					</NavLink>
				</div>
				<div>
					{
						user.followed
							? <button disabled={followingInProgress.some(id => id === user.id)} onClick={() => {
								unfollow(user.id);
							}}>Unfollow</button>
							: <button disabled={followingInProgress.some(id => id === user.id)} onClick={() => {
								follow(user.id);
							}}>Follow</button>
					}
				</div>
			</span>
			<span>
				<span>
					<div>
						{user.name}
					</div>
					<div>
						{user.status}
					</div>
				</span>
				<span>
					<div>
						{"u.location.country"}
					</div>
					<div>
						{"u.location.city"}
					</div>
				</span>
			</span>
		</div>
	)
}
